/**
 *  tabs 
 * @param  {String} type     
 * @return {Array}           
 */
export const leaderboards_query_list = function(Bmob, type, page) {
	return new Promise((resolve, reject) => {
		
		const query = Bmob.Query('CGP_HotRecommend')
		query.equalTo("type", "==", type)
		// readCount
		query.order("-readCount")
		query.limit(20)
		// query.skip(page * 10)
		query.find().then(res => {
			// console.log(res)
			resolve(res)
		});

	})
}