// html
export const formattingStr = function(str) {
	return str == null ? '' : str.replace(/<\/?.+?>/g, "").replace(/\ +/g, "").replace(/[\r\n]/g, "")
}

// 
export const bjDate = function(date, date1) {
	var date = new Date(date);
	var date1 = new Date(date1);
	if (date.getTime() - date1.getTime() < 0) {
		return '';
	} else {
		return '';
	}
}

// firefox chrome    
export const dateDifference = function(startTime, endTime, diffType) {
	//xxxx-xx-xx xxxx/xx/xx 
	startTime = startTime.replace(/\-/g, "/");
	endTime = endTime.replace(/\-/g, "/");
	//
	diffType = diffType.toLowerCase();
	var sTime = new Date(startTime); //
	var eTime = new Date(endTime); //
	//
	var timeType = 1;
	switch (diffType) {
		case "second":
			timeType = 1000;
			break;
		case "minute":
			timeType = 1000 * 60;
			break;
		case "hour":
			timeType = 1000 * 3600;
			break;
		case "day":
			timeType = 1000 * 3600 * 24;
			break;
		default:
			break;
	}
	return parseInt((eTime.getTime() - sTime.getTime()) / parseInt(timeType));
};

//  key  
export const getStorage = function(key) {
	return new Promise((resolve, reject) => {

		uni.getStorage({
			key: key,
			success(res) {
				resolve(res.data)
			},
			fail() {
				resolve(null)
			}
		})

	})
}

//  key  key  
export const setStorage = function(key, data) {
	return new Promise((resolve, reject) => {

		uni.setStorage({
			key: key,
			data: data,
			success() {
				resolve(true)
			},
			fail() {
				resolve(false)
			}
		})

	})
}

export const rgbRandom = function() { //rgb
	var r = Math.floor(Math.random() * 256);
	var g = Math.floor(Math.random() * 256);
	var b = Math.floor(Math.random() * 256);
	var rgb = '(' + r + ',' + g + ',' + b + ')';
	return rgb;
}

export const color16Random = function() { //
	var r = Math.floor(Math.random() * 256);
	var g = Math.floor(Math.random() * 256);
	var b = Math.floor(Math.random() * 256);
	var color = '#' + r.toString(16) + g.toString(16) + b.toString(16);
	return color;
}